<template>
  <div class="mt72">
    <div class="bigBox">
      <div class="boxLeft">
        <div class="ingBox">
          <img src="../../../../static/images/教师个人信息.png" alt />
        </div>
        <p>
          <span>身份ID：</span>
          <span>221347411</span>
        </p>
        <p>
          <span>姓名：</span>
          <span>陈老师</span>
        </p>
        <p>
          性别:
