        </p>
        <p>
          <span>学校:</span>
          <span>深圳水贝小学</span>
        </p>
        <p>
          <span>学段:</span>
          <span>初中部</span>
        </p>
        <p>
          <span>学科:</span>
          <span>数学</span>
        </p>
        <p>
          <span>身份证号码:</span>
          <span>44162211111111411</span>
        </p>
        <p>
          <span>年龄:</span>
          <span>18</span>
        </p>
        <p>
          <span>政治面貌:</span>
          <span>团员</span>
        </p>
      </div>
      <div class="boxRight">
        <div class="iconBox">
          <img src="../../../../static/images/头像.jpg" alt />
          <span>编辑头像</span>
        </div>
        <div class="msgBox">
          <el-form
            :label-position="labelPosition"
            label-width="100px"
            :model="formLabelAlign"
            style="width:50%;padding-left:150px"
          >
            <el-form-item label="姓名：">
              <el-input v-model="formLabelAlign.name"></el-input>
            </el-form-item>
            <el-form-item label="学校：">
              <el-input v-model="formLabelAlign.region"></el-input>
            </el-form-item>
            <el-form-item label="性别：">
              <el-radio v-model="radio" label="1">男</el-radio>
              <el-radio v-model="radios" label="1">女</el-radio>
            </el-form-item>
            <el-form-item label="学段：">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="学科：">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in option"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="身份证号码：">
              <el-input v-model="formLabelAlign.id"></el-input>
            </el-form-item>
            <el-form-item label="年龄：">
              <el-input v-model="formLabelAlign.age"></el-input>
            </el-form-item>
            <el-form-item label="政治面貌：">
              <el-input v-model="formLabelAlign.proloti"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button style="background-color:#2ab8b3;color:#fff;border:0;width:150px">提交</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      radio: "1",
      radios: "2",
      options: [
        { value: "1", label: "小学" },
        { value: "2", label: "初中" },
        { value: "3", label: "高中" }
      ],
      option: [
        { value: "1", label: "语文" },
        { value: "2", label: "数学" },
        { value: "3", label: "英语" }
      ],
      labelPosition: "right",
      value: "",
      formLabelAlign: {
        name: "",
        school: "",
        type: "",
        id: "",
        age: "",
        proloti: ""
      }
    };
  }
};
</script>
<style lang="less" scoped>
.bigBox {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  .boxLeft {
    width: 484px;
    border-right: 1px solid #eee;
    img {
      margin-bottom: 20px;
    }
    p {
      font-size: 16px;
      padding-left: 30%;
      text-align: left;
      margin: 20px;
    }
  }
  .boxRight {
    flex: 1;
    .iconBox {
      text-align: center;
      img {
        margin-top: 20px;
        width: 80px;
        height: 80px;
        border-radius: 50%;
      }
      span {
        display: block;
        font-size: 14px;
        color: #ccc;
        margin-bottom: 20px;
      }
    }
  }
}
</style>


